import http from '../http'



//项目列表
export const getProjectListByEmployeeId = () => {
    return http({
        url: '/jsb/getProjectListByEmployeeId',
        method: 'get',
    })
}

//项目详情
export const getProjectInfoByProjectId = (data) => {
    return http({
        url: '/jsb/getProjectInfoByProjectId',
        method: 'get',
        params:data
    })
}

