import http from '../http'



export const authLogin = (data) => {
    return http({
        url: '/jsb/authLogin',
        method: 'get',
        params:data,
        auth:true
        // data
    })
}
export const getUserByAuthcode = (data) => {
    return http({
        url: '/jsb/getUserByAuthcode',
        method: 'get',
        params:data,
        auth:true
    })
}

