import http from '../http'


export const getCityList = () => {//城市列表
    return http({
        url: 'jsb/complaint/areaList',
        method: 'get',
    })
}

export const getAreaList = (data) => {//区县列表
    return http({
        url: 'jsb/complaint/areaList',
        method: 'get',
        params:data
    })
}

export const getIndustry = () => {//行业主管部门列表
    return http({
        url: 'jsb/complaint/industryList',
        method: 'get',
    })
}

export const queryProject = (data) => {//项目查询
    return http({
        url: 'jsb/complaint/queryProject',
        method: 'get',
        params:data
    })
}

export const complaintSave = (data) => {//投诉提交
    return http({
        url: 'jsb/complaint/save',
        method: 'post',
        isFormData:true,
        data
    })
}